body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  height: 40px;
}

.nav_list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav_items:not(:last-child) {
  margin-right: 20px;
}

.nav_link {
  text-transform: uppercase;
  color: #000;
  height: 60px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav_link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
